@import url("https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@100;200;300;400;500;600;700&display=swap");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: "Josefin Sans", sans-serif !important;
}

.background {
  width: 100vw;
  height: 100vh;
  background: #f75050;
  display: flex;
  justify-content: center;
  align-items: center;
}

.app {
  width: 23vw;
  background: white;
  border-radius: 3vw;
  box-shadow: 0px 0px 16px 1px #00000030;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
}

.app_header {
  height: 5vw;
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  padding: 1.5vw 2vw;

  .bar {
    background: #d0d0d0;
    height: 0.5vw;
    width: 5vw;
    border-radius: 5vw;
    margin-right: 0.3vw;
  }
  .dot {
    display: inline-block;
    background: #d0d0d0;
    height: 0.5vw;
    width: 0.5vw;
    border-radius: 50%;
  }
}

.alarms {
  width: 100%;
  padding: 0 2vw;
  flex-grow: 1;

  label {
    color: gray;
    font-size: 0.9vw;
    font-weight: 300;
    display: inline-block;
    margin-bottom: 1vw;
  }
}

.alarm_list {
  li {
    list-style: none;
    padding: 0.7vw 0;
    border-bottom: 0.05vw solid whitesmoke;
  }
  .title {
    font-size: 1vw;
    font-weight: 400;
    color: gray;
  }
  .time {
    font-size: 2.2vw;
    color: gray;
    font-weight: 500;
    line-height: 1.3;
  }
  .days {
    font-size: 0.9vw;
    font-weight: 200;
    color: gray;
  }
}

.off_style {
  .title {
    color: #cccccc;
  }
  .time {
    color: #cccccc;
  }
  .days {
    color: #cccccc;
  }
}

.time_row {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  background: none;
  outline: none;
  border: none;
}

.switch {
  display: inline-block;
  &:hover {
    cursor: pointer;
  }
  .line {
    position: relative;
    display: inline-block;
    width: 2.8vw;
    height: 1vw;
    background: #efefef;
    border-radius: 5vw;
  }
  .circle {
    display: inline-block;
    width: 1.4vw;
    height: 1.4vw;
    background: #ffffff;
    border-radius: 50%;
    position: absolute;
    top: -0.3vw;
    box-shadow: 1px 1px 4px gainsboro;
    animation: switchOff 0.4s forwards;
  }

  .on {
    background: #565656;
    animation: switchOn 0.4s forwards;
  }
}

@keyframes switchOn {
  0% {
    right: 50%;
  }
  100% {
    right: 0%;
  }
}

@keyframes switchOff {
  0% {
    left: 50%;
  }
  100% {
    left: 0%;
  }
}

.tabs_row {
  display: flex;
  justify-content: space-between;
  padding: 2vw;
  margin-top: 8vw;

  .tab {
    list-style: none;
    position: relative;
    width: 30%;
    text-align: center;

    &::after {
      content: "";
      position: absolute;
      bottom: -0.5vw;
      left: 0;
      background: #efefef;
      height: 0.2vw;
      width: 100%;
      border-radius: 5vw;
    }
  }
  .tab_name {
    color: #e0e0e0;
    font-size: 1vw;
    font-weight: 300;
  }

  .active {
    &::after {
      background: gray;
    }

    .tab_name {
      color: gray;
    }
  }
}
